#container {
  margin: auto;
  margin-top: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  resize: both;
  width: 50%;
  height: 50% !important;
  background: rgba(0, 0, 0, 0.3); }
  #container .card-wrapper {
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    resize: both;
    width: 90%;
    height: 90%;
    border: 2px solid white;
    background: transparent; }
    #container .card-wrapper .card {
      margin: auto;
      padding: 0 20px;
      width: 90%;
      height: 90%;
      background: rgba(0, 0, 0, 0.3); }
      #container .card-wrapper .card #text {
        margin: 10% auto;
        font-size: 2vmax;
        text-align: center;
        color: white;
        line-height: 1.1;
        margin-bottom: 4%;
        -webkit-user-select: none;
           -moz-user-select: none;
            -ms-user-select: none;
                user-select: none; }
      #container .card-wrapper .card #Q {
        font-size: 4vmax;
        color: #e67300;
        -webkit-user-select: none;
           -moz-user-select: none;
            -ms-user-select: none;
                user-select: none; }
      #container .card-wrapper .card #subs {
        margin: 0% 2%;
        padding: 8px;
        display: flex;
        justify-content: space-between;
        background: rgba(0, 0, 0, 0.3); }
        #container .card-wrapper .card #subs #author {
          -webkit-user-select: none;
             -moz-user-select: none;
              -ms-user-select: none;
                  user-select: none;
          text-align: right;
          color: #e67300;
          font-size: 1.8vmax;
          line-height: 1.1; }
        #container .card-wrapper .card #subs #icons {
          width: 20%;
          display: flex;
          justify-content: space-between; }
          #container .card-wrapper .card #subs #icons #copy-quote {
            color: #e67300;
            font-size: 2vmax;
            cursor: pointer; }
          #container .card-wrapper .card #subs #icons #tweet-quote {
            color: #e67300;
            font-size: 2vmax;
            margin-left: -50%;
            cursor: pointer; }

#new-quote {
  width: auto;
  height: auto;
  padding: 2.5% 7%;
  color: white;
  font-size: 1.4vmax;
  font-weight: 600;
  text-align: center;
  cursor: pointer;
  position: absolute;
  top: 75%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  border: 1.5px solid #e67300;
  border-radius: 20px;
  line-height: 1.1;
  display: inline-block;
  outline: none;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
  background: transparent;
  box-shadow: 0px 4px 20px black; }
  #new-quote:hover {
    background: #e67300; }

.tooltiptext {
  opacity: 0;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  z-index: 1;
  cursor: pointer;
  -webkit-transition: all 2s ease;
  transition: all 2s ease; }

@media only screen and (max-width: 800px) {
  body #container {
    margin-top: 25%;
    width: 100%;
    height: 70vh; }
    body #container .card-wrapper {
      width: 100%;
      height: 100%; }
      body #container .card-wrapper .card {
        width: 100%;
        height: 100%; }
        body #container .card-wrapper .card #text {
          font-size: 4vmax; }
        body #container .card-wrapper .card #Q {
          font-size: 6vmax; }
        body #container .card-wrapper .card #subs #author {
          margin-left: 20%;
          font-size: 3.8vmax; }
        body #container .card-wrapper .card #subs #icons #copy-quote {
          font-size: 4vmax; }
        body #container .card-wrapper .card #subs #icons #tweet-quote {
          font-size: 4vmax; }
  body #subs .tooltiptext {
    width: 80px;
    margin-left: 2%;
    height: 25px !important;
    -webkit-transition: all 2s ease;
    transition: all 2s ease; }
  body #new-quote {
    font-size: 3vmax;
    border-width: 1px;
    width: 40%; } }

